.loginContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  height: 85vh;
  width: 100vw;
  text-align: center;
}
.loginForm {
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  height: 70vh;
  width: 60vw;
  max-width: 1000px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.loginWrapper {
  display: flex;
  flex-direction: column;
}

.loginLabel {
  font-size: 20px;
  font-weight: 300;
  padding: 1rem;
  color: #fff;
  text-align: center;
  border-radius: 10px;
}
.loginInput {
  text-align: center;
  width: 40vw;
  max-width: 600px;
  height: 50px;
  font-size: 18px;
  line-height: 20px;
  border-radius: 10px;
  outline: none;
  border: none;
}
.loginTitle {
  font-size: 40px;
  padding: 2rem;
  color: #fff;
  font-weight: 400;
}
.button {
  margin-top: 2rem;
  width: 400px;
  max-width: 400px;
  height: 60px;
  border-radius: 10px;
  border: none;
  background-color: rgb(23, 106, 155);
  color: #fff;
  font-size: 20px;
  transition: all 0.3s ease;
}
button:hover {
  background-color: rgb(0, 247, 255);
  /* color: black; */
  cursor: pointer;
}
.error-text {
  font-size: 30px;
}

@media(max-width: 500px){
  .loginForm{
    width: 95vw;
    height: 79vh;
  }
  .loginTitle{
    padding: 5px;
    font-size: 20px;
  }
  .loginInput{
    width: 250px;
    height: 40px;
    font-size: 16px;
    margin-bottom: 1rem;
  }
  .button{
    width: 200px;
    height: 40px;
    font-size: 16px;
    margin-top: 1rem;
  }
  .loginLabel{
    padding: 10px;
    padding-bottom: 0;
  }
  .loginWrapper{
    
  }
}
@media(max-width:780px){
  .button{
    margin-top: 15px;
    width: 40vw;
    height: 50px;
  }
  .loginLabel{
    padding: 10px;
  }
}
