.createHazardContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 85vh;
  }
  .createHazardTitle {
    color: #fff;
    font-weight: 400;
    font-size: 40px;
    padding: 2rem;
  }
  .formContainer {
    height: 65vh;
    background-color: rgba(0, 0, 0, 0.7);
  }
  .form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 60vw;
    padding: 2rem;
  }
  .label{
    font-size: 25px;
    padding: 1rem;
    color: #fff;
    text-align: center;
    /* margin-top: 1rem; */
    border-radius: 10px;
  }
  .input,
  .select {
    text-align: center;
    width: 40vw;
    max-width: 700px;
    height: 60px;
    font-size: 16px;
    line-height: 20px;
    border-radius: 10px;
    padding: 1rem;
    outline: none;
    border: none;
  }
  input:focus,
  select:focus {
    background-color: rgb(219, 224, 222);
    /* color: #fff; */
  }
  .select {
  }
  .textArea {
    width: 40vw;
    max-width: 700px;
    font-size: 16px;
    padding: 1rem;
    border-radius: 10px;
    resize: none;
    outline: none;
  }
  .button {
    margin-top: 2rem;
    width: 400px;
    max-width: 400px;
    height: 60px;
    border-radius: 10px;
    border: none;
    background-color: rgb(71, 165, 17);
    color: #fff;
    font-size: 25px;
    transition: all 0.3s ease;
  }
  button:hover {
    background-color: rgb(94, 255, 0);
    color: black;
    cursor: pointer;
  }
  .searchBox {
    display: flex;
    width: 40vw;
    max-width: 700px;
    height: 5vh;
    font-size: 16px;
    text-align: center;
    border: 3px solid black;
    border-radius: 10px;
    z-index: 50;
  }
  @media(max-width: 500px){
    .formContainer{
      height: auto;
    }
    .form{
      width: 95vw;
      height: 100%;
    }
    .label{
      font-size: 20px;
      font-weight: 300;
      padding: 10px;
    }
    #rndNum, .searchBox, .select, .textArea{
      width: 80vw;
    }
  }
  @media(max-width: 780px){
    .formContainer{
      height: auto;
    }
    .form{
      height: 100%;
      width: 80vw;
    }
    #rndNum, .searchBox, .select, .textArea{
      width: 60vw;
    }
  }