.deskNav{
  width: 100%;
  color: #fff;
  
}

.mobNav .navLinks {
  display: none;
  flex-direction: column;
  position: absolute;
  padding-top: 50px;
  top: 0;
  right: 0;
  width: 100%;
  height: 40vh;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 50;
}

.mobNav .links, .deskNav .links{
  font-size: 22px;
  letter-spacing: 1px;
  color: #fff;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  transition: all 0.3s ease;
}

.deskNav .navLinks {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 1rem;
}

.links:hover {
  color: rgb(0, 89, 255)!important;
}

.selected {
  color: rgb(127, 172, 255)!important;
}

.icon {
  display: none;
  position: absolute;
  top: 2%;
  right: 5%;
  font-size: 1.5rem;
  color: rgb(127, 172, 255);
  z-index: 100;
}
#signBtn{
  background: transparent;
  border: none;
}

@media (max-width: 700px){
  .deskNav {
    display: none;
  }
  .icon, .mobNav .navLinks{
    display: flex;
  }
}